*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
:root{
  --header:#5DADE2;
  --colorcode: 'Kanit', sans-serif;
}
header{
  width: 100vw;
  /* height: 10vh; */
  background-color: white;
}
.header-box{
  display: flex;
  padding: 40px;
  justify-content: space-between;
  align-items: center;
}
li{
  list-style: none;
  text-decoration: none;
  /* color: white; */
}
.input-set{
  border: none;
  padding: 6px;
  border-radius: 10px;
  outline: none;
}
.two-ul{
  display: flex;
  gap: 5%;
}
button{
  border:none;
  padding: 6px;
  background-color: white;
  border-radius: 5px;
}
.universal{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.color-box{
  height: 20vh;
  width: 20vw;
}
.colorplate{
  font-family: 'Inclusive Sans', sans-serif;
    font-size: 30px;
    font-weight: 700;
    width: 300px;
    gap: 5%;
}
input,button{
  border: none;
  outline: none;
  border-bottom: 3px black solid;
  padding: 5px;
  font-size: 15px;
}
.submitbtn{
  background: black;
  color: white;
  font-size: 20px;
  border-radius: 6px;
  padding: 10px;
  margin-left: 4px;
  cursor: pointer;
}
.main{
  padding: 50px;
}
.color-box h3,h4,p{
  font-family: var(--colorcode);
  padding: 10px;
  font-weight: lighter;
}
@media screen and (max-width: 550px) {
  .main {
    padding: 8px;
}
.color-box {
  height: 25vh;
  width: 26vw;
}
.color-box h3,h4,p{
  font-size: 15px;
}
.header-box{
  flex-direction: column;
  line-height: 3;
}
}